import React, { Component } from 'react';
import './RowDetailsSun.css';
import RowDetailsMoviesShowing from './RowDetailsMoviesShowing';

class RowDetailsSun extends Component {
  render() {
    const details = this.props.details;
    const index = Number(this.props.index);
    const sunCurrentPhaseIndex = Number(this.props.sunCurrentPhaseIndex);
    return (
      <tr className={index === sunCurrentPhaseIndex ? 'sun--highlight' : ''}>
        <td>{details.title}</td>
        <td>{details.range}</td>
      </tr>
    );
  }
}

export default RowDetailsSun;



