import React, { Component } from 'react';
import './Row.css';

class WidgetHeader extends Component {
  render() {
    return (
      <div className={`row-header${this.props.shouldWarn ? ' warn' : ''}`} onClick={this.props.toggleExpanded}>
        <div className="row-header-left">
          <div className="row-header-title">
            {this.props.title}
          </div>
          <div className="row-header-value-1">
            {this.props.value1}
          </div>
          <div className="row-header-value-2">
            {this.props.value2}
          </div>
        </div>
        <div className="row-header-right">
          <div className="row-header-icon">
            <img src={this.props.icon} alt=""/>
          </div>
        </div>
      </div>
    );
  }
}

export default WidgetHeader;



